@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.gpt3__blog-container_article{
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    background: var(--color-footer);
}

.gpt3__blog-container_article-image{
    width: 100%;
    height: 100%;
    background: var(--color-footer);
}

.gpt3__blog-container_article-image img{
    width: 100%;
    height: 100%;
}

.gpt3__blog-container_article-content{
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding: 1rem 1.5rem;
    height: 100%;
}

.gpt3__blog-container_article-content p{
    font-family: var(--font-family);
    font-size: 11.65px;
    line-height: 34.95px;
    font-weight: 700;
    color: white;
}

.gpt3__blog-container_article-content h3{
    font-family: var(--font-family);
    font-size: 25.11px;
    line-height: 30.29px;
    font-weight: 800;
    color: white;

    cursor: pointer;
}

.gpt3__blog-container_article-content p:last-child{
    cursor: pointer;
}

@media screen and (max-width: 550px) {
    .gpt3__blog-container_article-content h3{
        font-size: 18px;
        line-height: 25px;
    }
}
.gpt3__brand{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.gpt3__brand div{
    flex: 1 1;
    max-width: 150px;
    min-width: 120px;
    margin: 1rem ;

    display: flex;
    justify-content: center;
    align-items: center;
}
.gpt3__cta {
    color: white;
    
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;

    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
}

.gpt3__cta-content h3 {
    color: black;
    font-family: var(--font-family);
    font-size: 24px;
    font-weight: 800;
    line-height: 45px;
}

.gpt3__cta-content p {
    color: #0e0e0e;
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 30px;
    font-weight: 500;
}

.gpt3__cta-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    align-items: 2rem;
}

.gpt3__cta-btn button{
    background: black;
    border-radius: 40px;

    font-family: var(--font-family);
    font-size: 18px;
    font-weight: bold;
    line-height: 30px;

    color: white;
    
    padding: 0.5rem 1rem;
    
    border: none;
    outline: none;
    min-width: 150px;
    cursor: pointer;
}

.gpt3__cta-btn button:hover{
    transition: all 0.5s ease;
    background: rgb(65, 60, 60);
}

@media screen and (max-width: 650px) {
    .gpt3__cta{
        flex-direction: column; 
    }
    .gpt3__cta-btn{
        margin: 2rem 0 0;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__cta{
        margin: 2rem 4rem;
    }

    .gpt3__cta-content h3{
        font-size: 18px;
        line-height: 32px;
    }
    
    .gpt3__cta-btn button{
        font-size: 14px;
        line-height: 28px;
    }
}
.gpt3__features-container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;

    margin: 1rem;
}

.gpt3__features-container__feature-title {
    flex: 1 1;
    max-width: 180px;
    margin-right: 2rem;
}

.gpt3__features-container__feature-title h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 18px;
    line-height: 24px;

    letter-spacing: -0.04em;

    color: white;
}

.gpt3__features-container__feature-title div {
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.2);
    margin-bottom: 0.25rem;
}

.gpt3__features-container__feature-text {
    flex: 2 1;
    max-width: 390px;
    display: flex;
}

.gpt3__features-container__feature-text p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;

    color: var(--color-text);
}

@media screen and (max-width: 550px){
    .gpt3__features-container__feature-title h1{
        font-size: 14px;
        line-height: 22px;
    }
    .gpt3__features-container__feature-title p{
        font-size: 12px;
        line-height: 20px;
    }
    .gpt3__features-container__feature{
        margin: 1rem 0;
    }
}
.gpt3__navbar {
    display: flex;
    justify-self: space-between;
    align-items: center;

    padding: 2rem 6rem;
}

.gpt3__navbar-links {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.gpt3__navbar-links_logo {
    margin-right: 2rem;
}

.gpt3__navbar-links_logo img {
    width: 62.56px;
    height: 16.02px;
}

.gpt3__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.gpt3__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.gpt3__navbar-links_container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer
}

.gpt3__navbar-sign button,
.gpt3__navbar-links_container button,
.gpt3__navbar-menu_container-links-sign button{
    padding: 0.5rem 1rem;
    color: #fff;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: 500;
    border-radius: 5px;
    font-size: 18px;
    line-height: 25px;
    border: none;
    outline: none;
    cursor: pointer;
}

.gpt3__navbar-menu{
    margin-left: 1rem;
    display: none;
    position: relative;
}

.gpt3__navbar-menu svg{
    cursor: pointer;
}

.gpt3__navbar-menu_container{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2);
}

.gpt3__navbar-menu_container p{
    margin: 1rem 0;
}

.gpt3__navbar-menu_container-links-sign{
    display: none;
}

@media screen and (max-width: 1050px){
    .gpt3__navbar-links_container {
        display: none;
    }
    .gpt3__navbar-menu{
        display: flex;
    }
}

@media screen and (max-width: 700px){
    .gpt3__navbar{
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 700px){
    .gpt3__navbar{
        padding: 2rem;
    }
    .gpt3__navbar-sign{
        display: none;
    }

    .gpt3__navbar-menu_container{
        top: 20px;
    }

    .gpt3__navbar-menu_container-links-sign{
        display: block;
    }
}


.gpt3__blog {
    display: flex;
    flex-direction: column;
}

.gpt3__blog-heading {
    width: 100%;
    text-align: left;

    margin-bottom: 5rem;
}

.gpt3__blog-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.gpt3__blog-container {
    display: flex;
    flex-direction: row;
}

.gpt3__blog-container_groupA {
    flex: 0.75 1;
    margin-right: 2rem;
}

.gpt3__blog-container_groupB {
    flex: 1 1;

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
}

@media screen and (max-width: 990px) {
    .gpt3__blog-container{
        flex-direction: column-reverse;
    }
    .gpt3__blog-container_groupA{
        margin: 2rem 0;
    }
    .gpt3__blog-container_groupA .gpt3__blog-container_article{
        width: 48%
    }
    .gpt3__blog-container_groupA .gpt3__blog-container_article-image{
        height: 250px
    }
    
}

@media screen and (max-width: 700px){
    .gpt3__blog-container_groupB{
        grid-template-columns: repeat(1, 1fr);
    }
    .gpt3__blog-container_groupA .gpt3__blog-container_article{
        width: 100%
    }
    .gpt3__blog-heading h1{
        font-size: 46px;
        line-height: 52px;
    }
}

@media screen and (max-width: 550px){
    .gpt3__blog-heading h1{
        font-size: 34px;
        line-height: 42px;
    }
}
.gpt3__features{
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.gpt3__features-heading{
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    
    text-align: left;
    margin-right: 5rem;
}

.gpt3__features-heading h1{
    font-size: 34px;
    font-family: var(--font-family);
    line-height: 45px;
    font-weight: 800;
}

.gpt3__features-heading p{
    font-size: 16px;
    font-family: var(--font-family);
    line-height: 30px;
    font-weight: 500;

    color: var(--color-subtext);

    margin-top: 2rem;
}

.gpt3__features-container{
    flex: 1.5 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

@media screen and (max-width: 990px) {
    .gpt3__features{
        flex-direction: column;
    }
    .gpt3__features-heading{
        margin: 0 0 2rem 0;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__features-heading h1{
        font-size: 28px;
        line-height: 38px;
    }
}
.gpt3__footer {
    display: flex;
    color: white;

    flex-direction: column;
    justify-content: center;
    align-items: center;

    background: var(--color-footer);
}

.gpt3__footer-heading {
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;
}

.gpt3__footer-heading h1 {
    font-family: var(--font-family);
    font-size: 62px;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.gpt3__footer-btn button{
    box-sizing: border-box;
    font-family: var(--font-family);
    padding: 1rem;
    font-size: 18px;
    margin-bottom: 10rem;
    border: 1px solid white;
    word-spacing: 2px;
    line-height: 21px;
    color: white;
    background: transparent;
}

.gpt3__footer-btn button:hover{
    transition: all 0.5s ease;
    background: white;
    color: black;
    cursor: pointer;
}

.gpt3__footer-links{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    text-align: left;
}

.gpt3__footer-links div{
    width: 250px;
    margin: 1rem;
}

.gpt3__footer-links_logo{
    display: flex;
    flex-direction: column;
}

.gpt3__footer-links_logo img{
    width: 118px;
    height: 30px;

    margin-bottom: 1rem;
}

.gpt3__footer-links_logo p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: white;
}

.gpt3__footer-links_div{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.gpt3__footer-links_div h4{
    line-height: 16px;
    color: white;
    margin-bottom: 1rem;
    font-family: var(--font-family);
    font-size: 14px;
    line-height: 16px;
}

.gpt3__footer-links_div p{
    line-height: 16px;
    color: white;
    margin: 0.5rem 0rem;
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    cursor: pointer;
}

.gpt3__footer-copyright{
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.gpt3__footer-copyright p{
    line-height: 16px;
    color: white;
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
}

@media screen and (max-width: 850px) {
    .gpt3__footer-heading h1{
        font-size: 44px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px){
    .gpt3__footer-heading h1{
        font-size: 34px;
        line-height: 42px;
    }
    .gpt3__footer-links{
        margin: 1rem 0;
    }
    .gpt3__footer-btn p{
        font-size: 14px;
        line-height: 20px;
    }
}

@media screen and (max-width: 550px){
    .gpt3__footer-heading h1{
        font-size: 27px;
        line-height: 38px;
    }
}

.gpt3__header {
    display: flex;
    flex-direction: row;
}

.gpt3__header-content {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

    margin-right: 5rem;
}

.gpt3__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;

    letter-spacing: -0.04em;
}

.gpt3__header-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    color: var(--color-text);

    margin-top: 1.5rem;
}

.gpt3__header-content_input {
    width: 100%;
    margin: 2rem 0 1rem;

    display: flex;
    flex-direction: row;
}

.gpt3__header-content_input input {
    flex: 2 1;
    width: 100%;
    min-height: 50px;
    background: var(--color-footer);
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    font-family: var(--font-family);
    font-weight: normal;
    line-height: 27px;
    font-size: 20px;
    color: #fff;
    padding: 0 1rem;
    outline: none;
    border: 2px solid var(--color-footer);
}

.gpt3__header-content_input button {
    flex: 0.6 1;
    width: 100%;
    min-height: 50px;

    background-color: #FF4820;
    border-radius: 5px 0 0 5px;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 20px;
    line-height: 27px;
    color: white;
    cursor: pointer;
    outline: none;
    padding: 0 1rem;
    border: none;

    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.gpt3__header-content_input button:hover{
    transition: all 0.5s ease;
    background-color: #ff886d;
}

.gpt3__header-content_people {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;

    margin-top: 2rem;
}

.gpt3__header-content_people img {
    width: 181.79px;
    height: 38px;
}

.gpt3__header-content_people p {
    margin: 0 0 0 1rem;
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 38px;
    text-align: center;

    color: white;
}

.gpt3__header-image {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.gpt3__header-image img {
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 1250px) {
    .gpt3__header {
        flex-direction: columns;
    }

    .gpt3__header-content {
        margin: 0 0 3rem;
    }
    .gpt3__header-image{
        display: none;
    }
}

@media screen and (max-width: 650px) {
    .gpt3__header-content h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .gpt3__header-content p {
        font-size: 16px;
        line-height: 24px;
    }

    .gpt3__header-content_people {
        flex-direction: column;
    }

    .gpt3__header-content_people p {
        margin: 0;
    }

    .gpt3__header-content_input input,
    .gpt3__header-content_input button {
        font-size: 16px;
        line-height: 24px;
    }
    
    .gpt3__header-image{
        display: none;
    }
}


@media screen and (max-width: 490px) {
    .gpt3__header h1 {
        font-size: 36px;
        line-height: 48px;
    }

    .gpt3__header p {
        font-size: 14px;
        line-height: 24px;
    }

    .gpt3__header-content_input input,
    .gpt3__header-content_input button {
        font-size: 12px;
        line-height: 16px;
    }
    
    .gpt3__header-image{
        display: none;
    }
}
.gpt3__possibility{
    display: flex;
    flex-direction: row;
}

.gpt3__possibility-image{
    flex: 1 1;
    display: flex;
    justify-content:flex-start;
    align-items: center;

    margin-right: 2rem;
}

.gpt3__possibility-image img{
    width: 100%;
    height: 100%;
}

.gpt3__possibility-content{
    display: flex;
    flex: 1 1;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.gpt3__possibility-content h4{
    color: var(--color-text);
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 500;
    line-height: 30px;

}

.gpt3__possibility-content h4:last-child{
    color: var(--color-subtext);
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 500;
    line-height: 30px;
}

.gpt3__possibility-content h1{
    font-family: var(--font-family);
    font-size: 34px;
    font-weight: 800;
    line-height: 45px;   

    margin: 1rem 0;
}


.gpt3__possibility-content p{
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 30px;
    font-weight: 300;
    color: var(--color-text)
}

@media screen and (max-width: 1250px) {
    .gpt3__possibility-image{
        margin: 1rem 0;
    }
    .gpt3__possibility{
        flex-direction: column;
    }
    .gpt3__possibility-content{
        margin-top: 2rem;
    }
}
