.gpt3__footer {
    display: flex;
    color: white;

    flex-direction: column;
    justify-content: center;
    align-items: center;

    background: var(--color-footer);
}

.gpt3__footer-heading {
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;
}

.gpt3__footer-heading h1 {
    font-family: var(--font-family);
    font-size: 62px;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.gpt3__footer-btn button{
    box-sizing: border-box;
    font-family: var(--font-family);
    padding: 1rem;
    font-size: 18px;
    margin-bottom: 10rem;
    border: 1px solid white;
    word-spacing: 2px;
    line-height: 21px;
    color: white;
    background: transparent;
}

.gpt3__footer-btn button:hover{
    transition: all 0.5s ease;
    background: white;
    color: black;
    cursor: pointer;
}

.gpt3__footer-links{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    text-align: left;
}

.gpt3__footer-links div{
    width: 250px;
    margin: 1rem;
}

.gpt3__footer-links_logo{
    display: flex;
    flex-direction: column;
}

.gpt3__footer-links_logo img{
    width: 118px;
    height: 30px;

    margin-bottom: 1rem;
}

.gpt3__footer-links_logo p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: white;
}

.gpt3__footer-links_div{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.gpt3__footer-links_div h4{
    line-height: 16px;
    color: white;
    margin-bottom: 1rem;
    font-family: var(--font-family);
    font-size: 14px;
    line-height: 16px;
}

.gpt3__footer-links_div p{
    line-height: 16px;
    color: white;
    margin: 0.5rem 0rem;
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    cursor: pointer;
}

.gpt3__footer-copyright{
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.gpt3__footer-copyright p{
    line-height: 16px;
    color: white;
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
}

@media screen and (max-width: 850px) {
    .gpt3__footer-heading h1{
        font-size: 44px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px){
    .gpt3__footer-heading h1{
        font-size: 34px;
        line-height: 42px;
    }
    .gpt3__footer-links{
        margin: 1rem 0;
    }
    .gpt3__footer-btn p{
        font-size: 14px;
        line-height: 20px;
    }
}

@media screen and (max-width: 550px){
    .gpt3__footer-heading h1{
        font-size: 27px;
        line-height: 38px;
    }
}
